import React from 'react';
import Animate from 'react-smooth';

const About = () => {
    return (
        <Animate to="1" from="0" attributeName="opacity">
            <section className="about">
                <div className="container">
                    <h2 className="about__title title">About</h2>
                    <div className="about__wrapper">
                        <div className="about__img">
                            <img src="https://ucarecdn.com/10e31144-5ee3-4405-90ca-cb50d549a91a/" alt="main_photo"/>
                        </div>
                        <div className="about__description">
                            <h3 className="about__subtitle">My name is Anton</h3>
                            <p className="about__text">
                                I am a junior front-end developer. I have a basic command of English. I make up adaptive sites exactly according to the layout. After completing my work, I test my layout for
                                cross-browser compatibility, adaptability, validity, download speed. I have 
                                implemented several projects. I am improving my skills, 
                                learning something new for myself and I love my work very much.
                            </p>
                        </div>
                    </div>
                </div>
            </section>
        </Animate>
    )
}

export default About;